import React, { useEffect, useState } from "react";
import Text from "components/Text";
import UserList from "components/UserList";
import * as S from "./style";

const Favorites = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [favorites, setFavorites] = useState([]);

  useEffect(() => {
    getFavorites();
  }, []);

  const getFavorites = () => {
    setIsLoading(true);
    let favorites_data = [];
    const favorites_uuids = JSON.parse(localStorage.getItem('favorites')) || [];
    for(let uuid of favorites_uuids) {
      const favorite = JSON.parse(localStorage.getItem(uuid));
      favorites_data.push(favorite);
    }
    setFavorites(favorites_data);
    setIsLoading(false);
  }

  const removeFavoriteFromFavoritesPage = (uuid) => {
    const newFavorites = favorites.filter(user => user.login.uuid !== uuid);
    setFavorites(newFavorites);
  }

    return (
    <S.Home>
      <S.Content>
        <S.Header>
          <Text size="64px" bold>
            Favorites
          </Text>
        </S.Header>
        <UserList users={favorites} isLoading={isLoading}
                  page="favorites" removeFavoriteFromFavoritesPage={removeFavoriteFromFavoritesPage}/>
      </S.Content>
    </S.Home>
  );
};

export default Favorites;
